<template>
  <div class="title-second" @click="clickTitle">
    <div class="title-second-left">
      <div></div>
      <div :class="titleClass">{{title}}</div>
    </div>
    <div class="title-second-right" v-if="!hideRight">
      <div>更多</div>
      <div><img src="/static/images/commonicon/gd.png"/></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'titleSecond',
  props: {
    rightImgName: { type: String, default: 'gd', required: false },
    title: { type: String, default: '未知标题', required: false },
    hideRight: { type: Boolean, default: false, required: false },
    toPath: { type: String, default: '', required: false },
    size: {type: String, default: '', required: false}
  },
  computed: {
    rightImgUrl () {
      return `/static/images/commonicon/${this.rightImgName}.png`
    },
    titleClass () {
      return {
        smallsize: this.size === 'small'
      }
    }
  },
  methods: {
    clickTitle () {
      if (!this.hideRight) {
        // this.$toast.success('sss')
        this.$router.push({ path: this.toPath })
      }
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .title-second{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .title-second-left{
      display: flex;
      align-items: center;
      div:first-child{
        width: 10px;height: 30px;
        background-color: $green22ac38;
      }
      div:last-child{
        margin-left: 20px;
        color: $black333333;
        font-size: 30px;
        font-family: $font-family-default;
        font-weight: 600;
        line-height: 1.5;
      }
      .smallsize {
        font-size: 24px!important;
      }
    }
    .title-second-right{
      display: flex;
      align-items: center;
      div:first-child{
        color: $black999999;
        font-size: 26px;
      }
      div:last-child{
        margin-left: 20px;
        img{
          height: 28px;
        }
      }
    }
  }
</style>



