<template>
  <div class='page-header'>
    <van-nav-bar
      v-bind="$attrs" v-on="$listeners"
      @click-left="back"
      @click-right="toRightPath">
      <!--<div slot="left">-->
        <!--<slot name="left"></slot>-->
      <!--</div>-->
      <!--<div slot="title">-->
        <!--<slot name="title">{{title}}</slot>-->
      <!--</div>-->
      <div slot="right" v-if="!$attrs['right-text']">
        <slot name="right"></slot>
      </div>
    </van-nav-bar>
  </div>
</template>

<script>
// import ChartPie from '@/components/echarts/charts-pie/index'
export default {
  name: 'PageHeader',
  components: {},
  inheritAttrs: false,
  props: {
    backPath: {
      type: String,
      default: '',
      required: false
    },
    rightPath: {
      type: String,
      default: '',
      required: false
    }
  },
  data () {
    return {
      a: 'aaa'
    }
  },
  computed: {},
  created () {
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    toRightPath () {
      if (this.rightPath !== '') {
        this.$router.push({ path: this.rightPath })
      }
    }
  },
  watch: {}
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  .page-header {
    .van-nav-bar{
      height: 80px;
      line-height: 80px;
    }
  }
</style>



