<template>
  <div class="line" :style="style"></div>
</template>
<script>
export default {
  name: 'PageLine',
  components: {},
  props: {
    // L水平方向;V垂直方向
    direction: { type: String, default: 'L', required: false },
    length: { type: Number, default: 5, required: false },
    color: { type: String, default: '#999999', required: false },
    borderStyle: { type: String, default: 'solid', required: false },
    // 虚线间隔
    dashedSpacing: { type: Number, default: 0.5, required: false }
  },
  data () {
    return {}
  },
  computed: {
    style () {
      let res = ''
      let size = ''
      let border = ''
      if (this.direction === 'L') {
        size = `width:${this.length}rem;height:1px;`
        border = this.borderStyle === 'dashed'
          ? `background-image: linear-gradient(to right, #ffffff 50%, ${this.color} 50%);background-size: ${this.dashedSpacing}rem 1px;background-repeat: repeat-x;`
          : `border-top:1px ${this.borderStyle} ${this.color};`
      }
      if (this.direction === 'V') {
        size = `height:${this.length}rem;width:1px;`
        border = this.borderStyle === 'dashed'
          ? `background-image: linear-gradient(to bottom, #ffffff 50%, ${this.color} 50%);background-size: 1px ${this.dashedSpacing}rem;background-repeat: repeat-y;`
          : `border-left:1px ${this.borderStyle} ${this.color};`
      }
      res = size + border
      return res
    }
  },
  methods: {
    modifyParentPosition () {
      let parentPosition = this.$el.parentElement.style.position
      if (parentPosition === '' || parentPosition === 'static)') {
        this.$el.parentElement.style.position = 'relative'
      }
    }
  },
  created () {
  },
  mounted () {
    // this.modifyParentPosition()
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .line{
    box-sizing: content-box;
  }
</style>



